import React, { useState } from "react";
import {
  StyleSheet,
  TouchableWithoutFeedback,
  Modal,
  Button,
  ScrollView,
  View,
} from "react-native";
import * as Yup from "yup";

import Screen from "../components/Screen";
import useAuth from "../auth/useAuth";
import {
  ErrorMessage,
  Form,
  FormField,
  FormSwitchdbl,
  FormSwitchsng,
  SubmitButton,
} from "../components/forms";
import Text from "../components/Text";
import srvapi from "../api/srvapi";
import ActivityIndicator from "../components/ActivityIndicator";

const validationSchema = Yup.object().shape({
  userid: Yup.string()
    .required("     ")
    .label("User Id"),
  username: Yup.string()
    .required("    ")
    .label("User Name"),
  email: Yup.string()
    .required("    ")
    .email()
    .label("Email"),
  userpass: Yup.string()
    .required("      ")
    .min(4)
    .label("Password"),
  address: Yup.string()
    .required("       ")
    .label("Address"),
  postalcode: Yup.string()
    .required("     ")
    .label("Postalcode"),
  approve: Yup.bool()
    .oneOf([true], "    ")
    .label("Approve"),
});

function RegisterScreen() {
  const auth = useAuth();
  const [error, setError] = useState();
  const [actVisible, setActVisible] = useState(false);
  const [modalVisible, setModalVisible] = useState(false);
  const handleSubmit = async (userInfo) => {
    try {
      setActVisible(true);
      const result = await srvapi.post("/register", userInfo);
      setActVisible(false);
      auth.logIn(result.data.accessToken);
    } catch (err) {
      setActVisible(false);
      setError("    ");
    }
  };

  return (
    <>
      <ActivityIndicator visible={actVisible} />
      <Screen style={styles.container} scroll={true}>
        <Form
          initialValues={{
            userid: "",
            username: "",
            email: "",
            userpass: "",
            address: "",
            postalcode: "",
            telphone: "",
            province: "T",
            notifyflag: false,
            approve: false,
          }}
          onSubmit={handleSubmit}
          validationSchema={validationSchema}
        >
          <ErrorMessage error={error} visible={error} />
          <FormField
            autoCorrect={false}
            icon="account"
            name="userid"
            placeholder=" "
          />
          <FormField
            autoCorrect={false}
            icon="account"
            name="username"
            placeholder=" "
          />
          <FormField
            autoCapitalize="none"
            autoCorrect={false}
            icon="email"
            keyboardType="email-address"
            name="email"
            placeholder=""
            textContentType="emailAddress"
          />
          <FormField
            autoCapitalize="none"
            autoCorrect={false}
            icon="lock"
            name="userpass"
            placeholder=" "
            secureTextEntry
            textContentType="password"
          />
          <FormField
            autoCorrect={false}
            //icon="account"
            name="address"
            placeholder="  "
          />
          <FormField
            autoCorrect={false}
            //icon="account"
            name="postalcode"
            placeholder=" "
          />
          <FormField
            autoCorrect={false}
            //icon="account"
            name="telphone"
            placeholder=" "
          />
          <FormSwitchdbl
            switchTitle={["", " "]}
            switchValues={{ trueValue: "T", falseValue: "O" }}
            name="province"
          />
          <FormSwitchsng name="notifyflag" switchTitle="  " />
          <FormSwitchsng
            name="approve"
            switchTitle="        "
          />
          <TouchableWithoutFeedback onPress={() => setModalVisible(true)}>
            <Text style={styles.linktext}>  </Text>
          </TouchableWithoutFeedback>
          <SubmitButton title="" />
        </Form>
        <Modal visible={modalVisible} animationType="slide">
          <Button title="Close" onPress={() => setModalVisible(false)} />
          <ScrollView>
            <View>
              <Text style={styles.title} numberOfLines={1}>
                 
              </Text>
              <Text style={styles.text}>
                          
                           
                           
                .          
                     .      ,  
                            
                           
                       
              </Text>
              <Text style={styles.text}>
                           
                              
                           
                         
                .
              </Text>
              <Text style={styles.text}>
                           
                           
                    
              </Text>
              <Text style={styles.text}>
                           
                          
                    
              </Text>
              <Text style={styles.text}>
                           , 
                          
                  .     info@kanzhookstudio.ir 
                kanzhookstudio@gmail.com 
              </Text>
              <Text style={styles.text}>
                            
                         
                         
              </Text>
              <Text style={styles.text}>
                         
                           
                        .  
                           
                           
                            
                   .       
                           
                            
                   
              </Text>
              <Text style={styles.text}>
                          
                             
                        
              </Text>
              <Text style={styles.text}>
                             
                             
                
              </Text>
              <Text style={styles.text}>
                          
              </Text>
              <Text style={styles.texts}>
                  info@kanzhookstudio.ir  kanzhookstudio@gmail.com  
                     @kanzhook_studio
              </Text>
            </View>
          </ScrollView>
        </Modal>
      </Screen>
    </>
  );
}

const styles = StyleSheet.create({
  container: {
    padding: 10,
  },
  linktext: {
    color: "#3133f1",
    marginHorizontal: 15,
    marginBottom: 5,
  },
  title: {
    padding: 5,
    fontWeight: "600",
  },
  text: {
    padding: 5,
    fontWeight: "300",
    textAlign: "justify",
    marginHorizontal: 5,
  },
  texts: {
    padding: 5,
    fontWeight: "400",
    marginHorizontal: 5,
    marginBottom: 10,
  },
});

export default RegisterScreen;
